import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import SvgIcon from '@mui/material/SvgIcon';

const dispatchedEvents = [];

const SrsMuiSvgIcon = function (props, ref) {
  return (
    <SvgIcon {...props} theme={props.muiTheme}>
      <slot />
    </SvgIcon>
  );
}

const styles = `
  /* Custom styles for the SvgIcon component */
`;

const props = [
  {
    name: "component",
    defaultValue: "svg",
    type: "string",
  },
  {
    name: "color",
    defaultValue: "inherit",
    type: "string",
  },
  {
    name: "fontSize",
    defaultValue: "medium",
    type: "string",
  },
  {
    name: "htmlColor",
    defaultValue: "",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to SvgIcon if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(React.forwardRef(SrsMuiSvgIcon), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };
